<template lang="">
    <Accordion :theme="theme"></Accordion>
</template>
<script>

//import Header from "./components/Header.vue";
import Accordion from "../components/Accordion.vue";


export default {
    name: "Home",
    components: {
        Accordion
    },
    data: function () {
        return {
            theme: this.$route.name
        }
    },
    mounted: function () {
    },
    watch:{
        $route (to) {
            this.theme = to.name;
        }
    } 
}
</script>
