<template>
  <div class="app-wrapper">
    <div class="header-wrapper" ref="headerWrapper">
      <Header theme="light"></Header>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
import Header from "./components/Header.vue";

export default {
  name: "App",
  components: {
    Header
  },
  data: function () {
    return {
      theme: "light"
    }
  },
  mounted: function () {
    // setTimeout(() => {
    //   this.theme = "black"
    //   document.body.className = "dark";
    // }, 2000);
  },
  watch:{
    $route (to) {
        this.theme = to.name;
        document.body.className = this.theme;
        this.$refs.headerWrapper.className = `header-wrapper`;
        this.$refs.headerWrapper.classList.add(this.theme);
    }
  } 
  
};
</script>

